.burger-button
    width: 40px
    height: 40px
    border-radius: 50%
    background-color: $color-orange
    line-height: 40px !important
    text-align: center
    position: fixed
    z-index: 50
    left: 10px
    top: 10px
    color: white
    display: none
    +tablet
        display: block


.fa.fa-bars
    display: none
    +tablet
        display: block

.header__menu-buttons
    +tablet
        line-height: 0.7rem
        display: flex
        align-items: center
        justify-content: center
        flex-direction: column
        position: fixed
        background: rgba($color-orange, 0.9)
        z-index: 49
        top: 0
        left: -120vw
        bottom: 0
        width: 100%
        height: 100vh
        transition: .3s
        +smartphone
            margin: 0
            padding: 0
.is-active
    left: 0



