.values
    width: 100%
    height: 120vh
    background: url('../assets/PanelSolar.png')
    background-size: cover
    background-position: top
    margin: 40px 0
    display: grid
    grid-template-columns: 1fr
    grid-template-rows: auto
    grid-template-areas: ".values__title" ".values__wrapp" ".copyright"
    +tablet
        height: 100vh
    +xs
        height: 120vh


    .values__title
        grid-area: values__title
        text-transform: uppercase
        font-size: 1.1rem
        text-align: center
        margin-bottom: 0
        position: relative
        top: 25%
        color: white
        &::after
            content: ''
            position: absolute
            left: 47.8%
            top: 30px
            width: 5%
            background: $color-orange
            height: 7px
            +smartphone
                left: 41%
                width: 18%
            +xs
                left: 40.5%
                width: 20%

    .values__wrapp
        grid-area: values__wrapp
        width: 60%
        height: 90%
        background: rgba(white, .8 )
        margin: auto
        padding: 20px 60px
        text-align: center
        color: $color-primary
        display: flex
        flex-direction: column
        justify-content: center
        align-items: center
        +smartphone
            width: 100%
            padding: 20px 0

        .values__wrapp-title
            font-size: 3rem
            margin: 0
            padding-bottom: 20px
            font-weight: 400
            +smartphone
                display: flex
                justify-content: center
                font-size: 2rem

        .values__wrapp-p
            font-size: 1.2rem
            +smartphone
                font-size: 1rem
                padding: 0 20px

    .copyright
        grid-area: copyright
        text-align: center
        color: white
        font-size: .8rem
        +smartphone
            width: 100%
        p
            background: rgba(black, .5 )
